* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.hidden {
  display: none;
}

.header-bar {
  background: linear-gradient(90deg, #020024 0%, #096a79 52%, #00d4ff 87%);
  justify-content: center;
  margin-bottom: 10px;
  padding: 10px 0;
  display: flex;
}

.search-form input, .search-form button {
  padding: 5px;
}

[class^="icon-search"], [class*="icon-search"] {
  width: 20px;
  height: 10px;
  stroke-width: 0;
  stroke: #000;
  fill: #000;
  cursor: pointer;
  display: inline-block;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
  display: flex;
}

.photo-card {
  width: 350px;
  box-shadow: 6px 7px 12px 4px #42445a;
}

.photo-link img {
  height: 200px;
  width: 350px;
  object-fit: cover;
}

.photo-info {
  flex-direction: row;
  justify-content: space-between;
  gap: 15px;
  padding: 5px;
  display: flex;
}

.photo-info div {
  flex-direction: column;
  font-weight: bold;
  display: flex;
}

.photo-info p {
  font-style: italic;
}

.load-button {
  cursor: pointer;
  width: fit-content;
  visibility: hidden;
  background: rgba(14, 73, 149, .59);
  border: 1px solid #000;
  justify-content: center;
  margin: 20px auto;
  padding: 10px;
  font-weight: bold;
  display: flex;
}

.gallery-wrapper {
  flex-direction: column;
  display: flex;
}

